<h2>แสดงชื่อเล่น : {{nickname}}</h2>
<h2>แสดงอายุ : {{inputage}}</h2>
<h2>แสดงวัน/เดือน/ปีเกิด : {{birthday}}</h2>
<h2>แสดงสาขา : {{Major}}</h2>
<h2>แสดงห้อง : {{classroom}}</h2>
