<script>

export default {
  name: 'App',
  data(){
    return{
    firstname : "Nattaphon",
    lastname : "Somphrao",
    age : 18,
    fname : "",
    lname : "",
    nickname : "",
    inputage : "",
    birthday : "",
    Major : "",
    classroom : "",
    love : "",
    learn : "",
    object : "",
    }
  },
  methods:{
    showfirstname(){
      alert(this.firstname)
    },
    showlastname(){
      alert(this.lastname)
    },plus(){
      this.age++
    },
    minus(){
      this.age-=1
    },
    plusfive(val){
      this.age+=val
    },
    minusfive(val){
      this.age-=val
    },
    setfname(event){
      // console.log(event.target.value);
      this.fname = event.target.value
    },
    setlname(event){
      this.lname = event.target.value
    },
    setnickname(event){
      this.nickname = event.target.value
    },
    setinputage(event){
      this.inputage = event.target.value
    },
    setbirthday(event){
      this.birthday = event.target.value
    },
    setmajor(event){
      this.Major = event.target.value
    },
    setclassroom(event){
      this.classroom = event.target.value
    },
    setlove(event){
      this.love = event.target.value
    },
    setlearn(event){
      this.learn = event.target.value
    },
    setobject(event){
      this.object = event.target.value
    }
  }
}
</script>

